import {
	msgcontentPages,
	msgcontentAdd,
	msgcontentUpdate,
	msgcontentDelete,
	//	msgcontentDetail
} from 'api/api';
import storyform from './forms/messageDetailsForm';
let pageData = {
	msgContent:'',
	msgEnUsContent:'',
	msgEnUsTitle:'',
	msgGroup:'',
	msgId:'',
	msgJaJpContent:'',
	msgJaJpTitle:'',
	msgJoinUserList:'',
	msgKoKrContent:'',
	msgKoKrTitle:'',
	msgTime:'',
	msgTitle:'',
	msgType:'',
	msgZhCnContent:'',
	msgZhCnTitle:'',
	noticeType:'',
	status:'',
	templateId:'',
	userId:'',
}
export default {
	data() {
		return {
			searchValue: '',
			tableData: {},
			dialogVisible: false,
			dialogTyle: '',
			page: {
				start: 0,
				length: 10,
				draw: 1,
			},
			currentPage: 1,
			pageSizes: [10, 20, 30],

			pageData: {
				...pageData
			},
		}
	},
	components: {
		storyform,
	},
	created() {
		this.init()
	},
	watch: {
		searchValue(value) {
			if(!value) {//msgTitle
				msgcontentPages(this.page).then(res => {
					this.tableData = res.data.obj
				})
			}
		}
	},
	methods: {
		init() {
			msgcontentPages(this.page).then(res => {
				this.tableData = res.data.obj
			})
		},
		onQuery() {
			if(this.searchValue) {
				msgcontentPages({ ...this.page,
					msgTitle: this.searchValue
				}).then(res => {
					this.tableData = res.data.obj
				})
			}
		},
		showDilog(type, obj = pageData) {
			this.dialogVisible = true
			this.dialogTyle = type
			this.pageData = { ...obj
			}

		},
		onSubmit() {
			this.$refs.storyForm.validate(valid => valid && this.commitForm())
		},
		commitForm() {
			for(let key in this.pageData) {
				if(key.indexOf('Time') > -1) {
					delete this.pageData[key]
				}
			}
			if(this.dialogTyle === 'edit') {

				msgcontentUpdate(this.pageData).then(res => {
					this.init()
					this.dialogVisible = false
				})
			}else if(this.dialogTyle === 'add') {
				msgcontentAdd(this.pageData).then(res => {
					this.init()
					this.dialogVisible = false
				})
			}
		},
		handleDelete(obj) {
			this.$confirm(this.$t('commom.deleConfim'), '', {
				confirmButtonText: this.$t('commom.sure'),
				cancelButtonText: this.$t('commom.cancel'),
				type: 'warning'
			}).then(() => {

				msgcontentDelete({
					msgId: obj.msgId
				}).then(res => {
					this.init()
				})

			}).catch(res => {

			})

		},
		getpageData() {
			msgcontentPages({ ...this.page,
				msgTitle: this.searchValue
			}).then(res => {
				this.tableData = res.data.obj
			})
		},
		handleSizeChange(val) {
			this.page.length = val
			this.page.start = (this.currentPage - 1) * this.page.length
			this.getpageData()
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			this.page.start = (this.currentPage - 1) * this.page.length
			setTimeout(() => {
				this.getpageData()
			}, 0)
		},
	}
}



